import React from "react"
import styled from "styled-components"

const CardContainer = styled.div`
    width: 27rem;
    height: 100%;
    font-family: "Open Sans", sans-serif;
    color: white;
    background-image: url("https://images.pexels.com/photos/163016/crash-test-collision-60-km-h-distraction-163016.jpeg?cs=srgb&dl=red-and-yellow-hatchback-axa-crash-tests-163016.jpg&fm=jpg");
    background-size: 27rem auto;
    background-repeat: no-repeat;
    margin: 0rem 2rem 2rem 0rem;

    .title {
        font-size: 1.4rem;
    }
    .author {
        font-size: 0.9rem;
        font-weight: 700;
    }
    .timestamp {
        font-size: 0.7rem;
    }
    .content {
        padding: 30% 5% 2% 5%;
    }

    @media screen and (max-width: 1000px) {
        margin-top: 2rem;
        margin-bottom: 2rem;
    }

    @media screen and (max-width: 800px) {
        width: 22rem;
    }
`

const getDateLine = timestamp => {
    let dateArr = timestamp.split("T")[0].split("-")
    let monthList = [
        "Jan",
        "Feb",
        "March",
        "April",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec",
    ]

    let month = monthList[parseInt(dateArr[1]) - 1]

    let finalDate = `${dateArr[2]} ${month} ${dateArr[0]}`
    return finalDate
}

const SectionCard = ({ title, author, timestamp, blog_id }) => {
    return (
        <div>
            <a
                href={`/article?id=${blog_id}`}
                style={{ textDecoration: "none" }}
            >
                <CardContainer>
                    <div>
                        <div className="content">
                            <h4 className="title">{title}</h4>
                            <h6 className="author">
                                <b>{author}</b>
                            </h6>
                            <p className="timestamp">
                                {getDateLine(timestamp)}
                            </p>
                        </div>
                    </div>
                </CardContainer>
            </a>
        </div>
    )
}

export default SectionCard
