import React from "react"
import styled from "styled-components"

const CContainer = styled.div`
    display: flex;
    align-items: stretch;
    justify-content: center;
`

const ImageContainer = styled.div`
    width: 50%;
    height: auto;
    background-image: url("https://images.pexels.com/photos/163016/crash-test-collision-60-km-h-distraction-163016.jpeg?cs=srgb&dl=red-and-yellow-hatchback-axa-crash-tests-163016.jpg&fm=jpg");
    background-size: 100% 100%;
    background-repeat: no-repeat;
`

const TextContainer = styled.div`
    font-family: "Open Sans", sans-serif;
    width: 50%;
    height: 25rem;
    background-color: #dcdcdc;
    padding: 2%;
    h1 {
        font-size: 1.7rem;
    }

    h2 {
        font-size: 1.3rem;
        font-weight: bold;
    }

    p {
        font-size: 1.2rem;
    }

    h5 {
        font-weight: bold;
    }

    @media screen and (max-width: 1000px) {
        padding: 1%;
        height: auto;

        h1 {
            font-size: 1rem;
        }

        h2 {
            font-size: 0.7rem;
            font-weight: bold;
        }

        p {
            font-size: 0.7rem;
        }

        h5 {
            font-size: 0.6rem;
            font-weight: bold;
        }
        h6 {
            font-size: 0.5rem;
        }
    }
`

const Title = styled.div`
    @media screen and (min-width: 1000px) {
        position: absolute;
        top: 35%;
    }
`

const Author = styled.div`
    @media screen and (min-width: 1000px) {
        position: absolute;
        bottom: 10%;
    }
`

const getDateLine = timestamp => {
    let dateArr = timestamp.split("T")[0].split("-")
    let monthList = [
        "Jan",
        "Feb",
        "March",
        "April",
        "May",
        "June",
        "July",
        "Aug",
        "Sept",
        "Oct",
        "Nov",
        "Dec",
    ]

    let month = monthList[parseInt(dateArr[1]) - 1]

    let finalDate = `${dateArr[2]} ${month} ${dateArr[0]}`
    return finalDate
}

const CarouselContent = ({ title, sub_title, author, timestamp, blog_id }) => {
    return (
        <div>
            <a
                href={`/article?id=${blog_id}`}
                style={{ color: "black", textDecoration: "none" }}
            >
                <CContainer>
                    <ImageContainer></ImageContainer>
                    <TextContainer>
                        <h1>FEATURED</h1>
                        <Title>
                            <h2>{title}</h2>
                            <p>{sub_title}</p>
                        </Title>
                        <Author>
                            <h5>{author}</h5>
                            <h6>{getDateLine(timestamp)}</h6>
                        </Author>
                    </TextContainer>
                </CContainer>
            </a>
        </div>
    )
}

export default CarouselContent
